<script>
import BScroll from 'better-scroll';

export default {
  name: 'agreement',
  data () {
    return {
      scroll: '' // 
    }
  },
  methods: {
    back () {
      this.$router.push('/register')
    },
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.wrapper, {
          click: true,
          probeType: 3
        });
        console.log(this.scroll)
      } else {
        this.scroll.refresh();
      };
    }
  },
  mounted() {
    // this.scroll = new BScroll(this.$refs.wrapper)
    // console.log(this.scroll)
    this.$nextTick(()=>{
      this._initScroll();
    });
  }
}
</script>

<style lang="scss" scoped>
@import '@/styles/index.scss';
.header {
  position: relative;
  height: .44rem;
  line-height: .44rem;
  text-align: center;
  color: #fff;
  background: $bg;
  font-size: .17rem;
  .svg-container {
    position: absolute;
    left: 0;
    color: #fff;
    vertical-align: middle;
    width: .44rem;
    line-height: .44rem;
    display: inline-block;
    font-size: .2rem;
  }
}
.wrapper {
  overflow: hidden;
  position: absolute;
  z-index: 1;
  top: .44rem;
  left: 0;
  bottom: 0;
  right: 0;
  margin: 0 .15rem;
  .wrap {
    height: auto;
    .msg-title {
      margin: .1rem 0;
      padding-top: .08rem;
    }
    .msg-content {
      margin-top: .15rem;
      font-size: .14rem;
      line-height: .22rem;
      color: #333;
      p {
        margin-bottom: .1rem;
      }
      h3 {
        font-weight: bold;
        font-size: .22rem;
        margin: .05rem 0;
      }
    }
  }
}
</style>




